import { getTopAnime } from '@/lib/api'
import { Navbar } from '@/components/navbar'
import { AnimeGrid } from '@/components/anime-grid'

async function TrendingContent() {
  const data = await getTopAnime(1)
  const animes = data.data || []

  if (animes.length === 0) {
    return <p className="text-center text-muted-foreground">No trending anime found</p>
  }

  return <AnimeGrid animes={animes} title="Trending This Week" />
}

export default async function TrendingPage() {
  return (
    <main className="min-h-screen bg-background">
      <Navbar />

      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-12">
        <h1 className="text-4xl font-bold mb-2 text-foreground">Trending Anime</h1>
        <p className="text-muted-foreground mb-8">
          Discover what's trending in the anime community
        </p>
      </div>

      <TrendingContent />
    </main>
  )
}
